import AppContext from "../context"
import{useContext} from 'react'
export const useCart=()=>{
const {cartItems,setCartItems,favoriteItems} = useContext(AppContext)
const totalPrice=cartItems.reduce((sum,obj)=>obj.price+sum,0)
const countFavs=favoriteItems.length
return{cartItems,setCartItems,totalPrice,countFavs}
}